"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Image from "next/image";

import EventCarousel from "../components/EventCarousel";
import { fetchEvents } from "../lib/fetchEvents";

export default function Home() {
  const [events, setEvents] = useState([]);
  const [search, setSearch] = useState("");
  const [suggestions, setSuggestions] = useState([]);

  useEffect(() => {
    loadTrendingEvents();
  }, []);

  async function loadTrendingEvents() {
    const data = await fetchEvents("music");
    setEvents(data.slice(0, 15));
  }

  // Auto-complete live search
  useEffect(() => {
    if (!search.trim()) {
      setSuggestions([]);
      return;
    }

    const timer = setTimeout(async () => {
      const results = await fetchEvents(search);
      setSuggestions(results.slice(0, 8));
    }, 400);

    return () => clearTimeout(timer);
  }, [search]);

  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 via-gray-800 to-gray-900 text-white">

      {/* ================= HERO SLIDER ================= */}
      <section className="py-12">
        <EventCarousel events={events.slice(0, 6)} />
      </section>

      {/* ================= HERO TITLE + SEARCH ================= */}
      <section className="px-6 md:px-16 py-10 text-center">
        <h1 className="text-4xl md:text-6xl font-extrabold leading-tight">
          Find Your Next <span className="text-blue-500">Event</span>
        </h1>
        <p className="mt-4 text-gray-300 text-lg md:text-xl max-w-2xl mx-auto">
          Discover sports, concerts, and live shows happening around you.
        </p>

        {/* Search Bar */}
        <div className="mt-10 max-w-3xl mx-auto relative">
          <input
            type="text"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            placeholder="Search for events, teams, or venues..."
            className="w-full px-6 py-4 rounded-xl bg-gray-800 border border-gray-700 text-white focus:ring-2 focus:ring-blue-500 outline-none"
          />

          {/* ===== AUTOCOMPLETE BOX ===== */}
          {suggestions.length > 0 && (
            <div className="absolute left-0 right-0 mt-2 bg-gray-800 border border-gray-700 rounded-xl shadow-xl p-3 z-50 max-h-80 overflow-y-auto">
              {suggestions.map((ev) => (
                <Link key={ev.id} href={`/events/${ev.id}`}>
                  <div className="flex gap-3 p-2 hover:bg-gray-700 rounded-lg cursor-pointer">
                    <img
                      src={ev.images?.[0]?.url}
                      className="w-14 h-14 object-cover rounded-md"
                    />
                    <div>
                      <p className="font-semibold">{ev.name}</p>
                      <p className="text-gray-400 text-sm">
                        {ev.dates?.start?.localDate}
                      </p>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          )}
        </div>
      </section>

      {/* ================= CATEGORIES ================= */}
      <section className="px-6 md:px-16 py-14">
        <h2 className="text-3xl font-bold mb-8">Popular Categories</h2>

        <div className="grid grid-cols-2 md:grid-cols-4 gap-6">
          {[
            { name: "Sports", img: "/images/sports.jpg" },
            { name: "Concerts", img: "/images/concerts.jpg" },
            { name: "Theatre", img: "/images/theatre.jpg" },
            { name: "Festivals", img: "/images/festival.jpg" },
          ].map((item, index) => (
            <div
              key={index}
              className="relative rounded-xl overflow-hidden group cursor-pointer shadow-lg"
            >
              <Image
                src={item.img}
                width={400}
                height={300}
                alt={item.name}
                className="group-hover:scale-110 transition duration-500"
              />

              <div className="absolute inset-0 bg-black/40 flex items-center justify-center text-xl font-bold">
                {item.name}
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* ================= TRENDING EVENTS ================= */}
      <section className="px-6 md:px-16 py-14">
        <h2 className="text-3xl font-bold mb-6">Trending Events</h2>

        <EventCarousel events={events.slice(0, 12)} />
      </section>

      {/* ================= CTA SECTION ================= */}
      <section className="px-6 md:px-16 py-16 text-center bg-gray-800">
        <h2 className="text-3xl md:text-4xl font-bold">
          Never Miss an Event Again
        </h2>
        <p className="text-gray-400 mt-2">
          Subscribe for updates on your favorite teams and artists.
        </p>

        <button className="mt-6 px-8 py-3 bg-blue-600 hover:bg-blue-500 rounded-lg text-lg font-medium">
          Subscribe Now
        </button>
      </section>
    </div>
  );
}
